import Vue from 'vue'
import Vuex from 'vuex'
import profile from './modules/profile.js'
import newsFeed from './modules/newsFeed.js'
import admin from './modules/admin.js'
import hr from './modules/hr.js'
import calendar from './modules/calendar.js'
import member from './modules/member.js'
import chat from './modules/chat.js'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: () => ({
    isLoaded: false
  }),
  getters: {
    isLoaded: (state) => state.isLoaded
  },
  mutations: {
    dataLoaded: (state) => {
      state.isLoaded = true;
    },
    setApiData: (state, payload) => {
      state[payload.prefix][payload.name] = _.cloneDeep(payload.data);
    }
  },
  actions:{
    getApiData: async (context, payload) => {
      try{
          if(!payload) throw new Vue.prototype.BaseError(payload.name, "Payload is not provided");        
          const data = await Vue.prototype.$api.general.get(payload.webreportId, payload.params);
          context.commit('setApiData', {prefix: payload.prefix, name: payload.name, data: data});
      }catch(err){
          console.log(`${err.error_function}: ${err.message}`);
      }
    },
    initialLoad: async (context) => {
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'instructions',
        webreportId: opentext.webreportIds.childrenElementsId,
        params: `&parentid=${opentext.instructionId}`
      });
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'videoInstructions',
        webreportId: opentext.webreportIds.childrenElementsId,
        params: `&parentid=${opentext.videoFolder}`
      });
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'motivationHistories',
        webreportId: opentext.webreportIds.blogId,
        params: '&blogId='+ opentext.motivationHistories
      });
      await context.dispatch('getApiData', { 
        prefix: 'admin',
        name: 'generalNews',
        webreportId: opentext.webreportIds.newsId,
        params: '&parentId=889054'
      }); 
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'ceoMessages',
        webreportId: opentext.webreportIds.blogId,
        params: '&blogId='+ opentext.ceoMessage
      });
      await context.dispatch('getApiData', {
        prefix: 'hr',
        name: 'blogHeadlines',
        webreportId: opentext.webreportIds.blogHeadline,
        params: '&blogId='+ opentext.hrBlog
      });
      await context.dispatch('getApiData', {
        prefix: 'hr',
        name: 'vacancies',
        webreportId: opentext.webreportIds.newsId,
        params: '&parentId=876218'
      });
      context.commit('dataLoaded');
    }
  },
  modules: {
    profile,
    newsFeed,
    admin,
    hr,
    calendar,
    member,
    chat
  }
});

export default store;
